* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  font-size: 1.6rem;
  font-family: sans-serif;
}

.App {
  background-image: url("./assets/landing.jpg");
  background-size: cover;
  transition: 0.4s ease-out;
}

.App.rainy {
  background-image: url('./assets/rainy.jpg');
}

.App.sunny {
  background-image: url('./assets/sunny.jpg');
}

.App.stormy {
  background-image: url('./assets/stormy.jpg');
}

.App.snowy {
  background-image: url('./assets/snowy.jpg');
}

.App.fog {
  background-image: url('./assets/fog.jpg');
}

.App.drizzle {
  background-image: url('./assets/drizzle.jpg');
}

.App.mist {
  background-image: url('./assets/mist.jpg');
}

main {
  min-height: 100vh;
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.3));
  padding: 2.5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}

h1 {
  margin: 3.2rem;
  color: #fff;
  text-shadow: 2px 2px rgba(0, 0, 0, 0.564);
}

h3 {
  margin: 1.6rem;
  color: #fff;
}

.search-box {
  width: 100%;
  margin: 0 0 75px;
}

.search-box .search-bar {
  display: block;
  width: 50%;
  margin : 0 auto;
  padding: 1.6rem;
  appearance: none;
  background: none;
  border: none;
  outline: none;
  background-color: rgba(255, 255, 255, 0.75);
  border-radius: 0 0 1.6rem 1.6rem;
  font-family: sans-serif;
  font-size: 2rem;
  box-shadow: 0px 2px rgba(0, 0, 0, 0.3);
  color: #313131;
  transition: 0.4s ease;
}

.search-box .search-bar:focus {
  background-color: rgba(255, 255, 255, 0.9);
}

.location-box .location {
  color: #fff;
  font-size: 3.2rem;
  font-weight: 500;
  text-align: center;
  text-shadow: 2px 2px rgba(0, 0, 0, 0.564);
}

.location-box .date {
  color: #fff;
  font-size: 2rem;
  font-weight: 300;
  font-style: italic;
  text-align: center;
  margin: 1.6rem 0;
  text-shadow: 1px 1px rgba(0, 0, 0, 0.564);
}

.weather-box {
  text-align: center;
  color: #fff;
}

.weather-box .temperature {
  display: inline-block;
  font-size: 10rem;
  padding: 2.4rem;
  margin: 1.6rem 0;
  border-radius: 1.6rem;
  background-color: rgba(255, 255, 255, 0.2);
  font-weight: 700;
  text-shadow: 3px 3px rgba(0, 0, 0, 0.5);
  box-shadow: 3px 3px rgba(0, 0, 0, 0.2);
}

.weather-box .weather {
  color: #fff;
  font-size: 4.8rem;
  font-weight: 700;
  text-shadow: 2px 2px rgba(0, 0, 0, 0.5);
  margin: 1.6rem;
}

@media screen and (max-width: 550px) {
  .search-box .search-bar {
    width: 70%;
  }
}